import { React, useState } from "react";
import ListItem from "@material-ui/core/ListItem";

import ListItemText from "@material-ui/core/ListItemText";
import {
  makeStyles,
  Drawer,
  IconButton,
  List,
  Paper,
  Box,
  Typography,
} from "@material-ui/core";

import MenuIcon from "@material-ui/icons/Menu";

import CloseIcon from "@material-ui/icons/Close";

import { NavLink } from "react-router-dom";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: drawerWidth,
  },
  root: {
    width: "100vw",
    height: "100vh",
    display: "grid",
    justifyItems: "center",
    paddingTop: "10vh",
  },
  menuicon: {
    marginRight: "auto",
    // order:'1',
    // display:'flex',
    // flexGrow:1
  },
  drawItems: {
    textDecoration: "none",
    color: "navyblue",
    fontFamily: "Ubuntu",
    fontSize: "25px",
  },
  center: {
    justifyContent: "center",
  },
}));

const DrawerComponent = () => {
  const classes = useStyles();

  const onClose = ({ onClick }) => setopenDrawer(false);
  const [openDrawer, setopenDrawer] = useState(false);
  return (
    <div display="flex" className={classes.menuicon}>
      <Drawer
        anchor="left"
        onClose={() => setopenDrawer(false)}
        open={openDrawer}
      >
        <IconButton onClick={onClose}>
          <CloseIcon />
        </IconButton>
        <Paper component={Box} className={classes.root}>
          <List className={classes.drawer}>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/PixCorto">
                <ListItemText onClick={onClose}>
                  <Typography variant="h4">Home</Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/events">
                <ListItemText onClick={onClose}>
                  <Typography variant="h4">Events</Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/discussions">
                <ListItemText onClick={onClose}>
                  <Typography variant="h4">Discussions</Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/blogs">
                <ListItemText  onClick={onClose} >
               <Typography variant="h4">
                 Blogs
               </Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/contactus">
                {" "}
                <ListItemText onClick={onClose}>
                  <Typography variant="h4">
                    Contact Us
                  </Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
            <ListItem className={classes.center} button>
              <NavLink className={classes.drawItems} to="/subscription">
                {" "}
                <ListItemText  onClick={onClose} >
                  <Typography variant="h4">
                    Subscription
                  </Typography>
                </ListItemText>
              </NavLink>
            </ListItem>
          </List>
        </Paper>
      </Drawer>
      <div>
        <IconButton onClick={() => setopenDrawer(!openDrawer)}>
          <MenuIcon />
        </IconButton>
      </div>
    </div>
  );
};

export default DrawerComponent;
