import React from "react";
import "./App.css";
import { Route } from "react-router-dom";
import Home2 from "./components/Home2";
import Navbar from "./components/Navbar";
import ContactUs from "./components/ContactUs";
import Subcription from "./components/Subcription";
import Footer from "./components/Footer";
import Events from "./components/Events";
import Blogs from "./components/Blogs"
import Discussions from "./components/Discussions";
import Chat from "./components/Chat";
import MobileView from "./components/MobileView";
import ChatBody from "./components/chat/chatBody/ChatBody";


const App = () => {
  return (
    <div>
      <Navbar />

      <Route exact path="/PixCorto" component={Home2} />
      <Route exact path="/contactus" component={ContactUs} />
      <Route exact path="/subscription" component={Subcription} />
      <Route exact path="/events" component={Events} />
      <Route exact path="/blogs" component={Blogs} />
      <Route exact path="/discussions" component={Discussions} />
      <Route exact path="/discussions/chat" component={ChatBody} />
      <Route exact path="/mobile" component={MobileView}/>
      <Footer/>
     
    </div>
  );
};

export default App;
