import { Button, Container } from "@material-ui/core";
import {
  Grid,
  Card,
  CardActionArea,
  CardMedia,
  Typography,
  CardContent,
  makeStyles,
} from "@material-ui/core";
import React from "react";

const useStyles = makeStyles((theme) => ({
  card: {
    maxWidth: "100%",
    margin: "15px",
    minWidth:300,
    borderRadius:'15px'
  },
  media: {
    height: 240,
  },
  flex: {
    display: "flex",
    marginTop: "10vh",
    marginBottom: "10vh",
    marginLeft: "auto",
    marginRight: "auto",
    [theme.breakpoints.down("sm")]: {
     flexWrap: 'wrap',
    // width: '95%',
    },
    // flexWrap: 'wrap',
    // width: '95%',
  },
}));

const Discussions = () => {
  const classes = useStyles();
  return (
    <Container maxWidth="lg" className={classes.blogsContainer}>
      <Grid container spacing={3}>
        <Grid item className={classes.flex}>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Acting
                </Typography>
               <Button variant="contained" color="secondary" href="/chat">
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Art
                </Typography>
               <Button variant="contained" color="secondary" href="/chat">
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Music
                </Typography>
               <Button variant="contained" color="secondary" href="/chat">
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Discussions;
